import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCircleNotch } from '@fortawesome/free-solid-svg-icons';

function Loader(props) {
    let fixedStyle = {  position: 'fixed', 
                        backgroundColor: 'rgb(255, 255, 255, 0.5)', 
                        top: 0,
                        left: 0,
                        width: '100vw', 
                        height: '100vh', 
                        zIndex: 1,
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'center',
                    };

    let relativeStyle = {   position: 'absolute', 
                            backgroundColor: 'rgb(255, 255, 255, 0.5)', 
                            width: '100%', 
                            height: '100%', 
                            zIndex: 1,
                            display: 'flex',
                            alignItems: 'center',
                            justifyContent: 'center',
                        };

    let style = null;
    if(props.type === 'relative') {
        style = relativeStyle;
    }
    else if(props.type === 'fixed') {
        style = fixedStyle;
    }

    return (
        <div style = { style } >
            <FontAwesomeIcon icon={ faCircleNotch } style={ { color: 'DodgerBlue' } } className="default fa-spin" size="2x" />
        </div>
    );
}

export default Loader;